body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
}

.App {

  margin: 0 auto;
  display: inline-block;
}
#map {
  width:auto;
  height:2000px;
  max-height:100vh;
  max-width:100vw;
}

.wall.active,.overhatch.active {
  fill: #2e3;
  stroke: #2e3;
  mix-blend-mode: hue;
}
.wall,.overhatch {
  mix-blend-mode: hue;
  fill: rgb(238, 201, 34);
  stroke: rgb(238, 201, 34);
}
.wall:hover,.overhatch:hover {
  fill: #f00;
  stroke: #f00;
}

.underhatch.active {
  fill: #2e3;
  stroke: #2e3;
}
.underhatch {
  opacity: 90%;
  fill: rgb(238, 201, 34);
  stroke: rgb(238, 201, 34);
}
.underhatch:hover {
  fill: #f00;
  stroke: #f00;
}

nav {
  position: absolute;
}
nav > div {
  float:left;
}
nav button {
  border: 1px solid #000;
  font-size: 1.2rem;
  outline:none;
}
nav button.active {
  background-color: khaki;
}

.bomb1 {
  color: black;
  fill:   tomato;
  background-color:  tomato;
}
.bomb2 {
  color: black;
  fill:   gold;
  background-color:  gold;
}
.bomb3 {
  color: black;
  fill:   lightgreen;
  background-color:  lightgreen;
}
.bomb4 {
  color: white;
  fill:   royalblue;
  background-color:   royalblue;
}
.bombText {
  fill: black;
  
  font-family: Arial, Helvetica, sans-serif;
  font-size:20px;
}
#site-picker {
  height: 100%;
  font-size: 1.2rem;
  margin-left:1em
}
select {
  color:white;
}